.button {
  border: none;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  font-size: 1.3rem;
  font-weight: 600;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  cursor: pointer;
}

.icon {
  margin-right: 0.5rem;
}

.fill {
  background-color: #181818;
  color: #fce8ad;
  transition: all 0.3s;
}

.fill:hover {
  background-color: #318161;
  color: #fff;
}

@media screen and (max-width: 576px) {
  button {
    padding: 0.5rem 1rem;
  }

  .icon {
    margin-right: 0;
  }
}
